import {
  Component, ViewChildren, QueryList, ViewChild
} from '@angular/core';
import { IonicPage, NavController, Content, Slides, ToastController } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { Category } from '../../models/category';
import { Item } from '../../models/item';
import { CategoryService } from '../../service/category';
import { ItemService } from '../../service/item';

@IonicPage()
@Component({
  selector: 'page-agent',
  templateUrl: 'agent.html',
  providers: [StatusBar, CategoryService, ItemService]
})
export class AgentPage {

  @ViewChild(Content) content: Content;
  @ViewChildren(Slides) slides: QueryList<Slides>;
  categoryList: Category[] = [];

  pnum: number = 1;
  pqty: number = 10;

  errorMessage: string;

  constructor(public navCtrl: NavController, private statusBar: StatusBar, private categoryService: CategoryService, private itemService: ItemService, public toaCtrl: ToastController) {

  }

  enterItem(item: Item) {
    this.navCtrl.push('ItemPage', { item: item });
  }

  ionViewDidLoad() {

    //this.statusBar.styleDefault();
    this.getCategory();
  }

  enterMessagePage() {
    this.navCtrl.push('MessagePage');
  }

  getCategory(infiniteScroll?) {//获取首页专场列表及相应拍品列表

  }

  ngAfterViewInit() {
    //this.content.enableJsScroll();
    this.slides.map((item: Slides, index: number, array: Slides[]) => {
      item.slidesOffsetBefore = 24;
      item.spaceBetween = 12;
      item.slidesPerView = 'auto';
      item.slidesOffsetAfter = 24;
    });

    this.slides.changes.subscribe(item => {
      this.reRenderSlide();
    })
  }

  reRenderSlide() {
    this.slides.map((item: Slides, index: number, array: Slides[]) => {
      item.slidesOffsetBefore = 24;
      item.spaceBetween = 12;
      item.slidesPerView = 'auto';
      item.slidesOffsetAfter = 24;
      item.update(0);
    });
  }
}



